import React from 'react'
import styles from './style.module.css'
import classNames from 'classnames'

class MagicBox extends React.Component {
  constructor(props) {
    super(props)

    const { backgroundColor, highlightText } = this._generateRandomColor()

    this.state = {
      style: {
        backgroundColor,
      },
      highlightText,
    }
  }

  render() {
    const { style, highlightText } = this.state

    const className = classNames({
      [styles.Container]: true,
      [styles.HighlightText]: highlightText,
    })

    return (
      <div
        className={className}
        style={style}
        onClick={this._onClick}
      >
        MagicBox
      </div>
    )
  }

  _onClick = () => {
    const { backgroundColor, highlightText } = this._generateRandomColor()

    this.setState({
      style: {
        backgroundColor,
      },
      highlightText,
    })
  }

  _generateRandomColor() {
    const r = this._getRandomInt(0, 256)
    const g = this._getRandomInt(0, 256)
    const b = this._getRandomInt(0, 256)
    const a = Math.sqrt(this._getRandomArbitrary(0, 1))
    const darkest = [this._pow(r), this._pow(g), this._pow(b)].reduce((pv, cv) => pv + cv, 0)

    return {
      backgroundColor: `rgba(${r},${g},${b},${a})`,
      highlightText: (darkest > 123456) && (a > 0.78),
    }
  }

  _getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
  }

  _getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min
  }

  _pow(v) {
    return Math.pow(256 - v, 2)
  }
}

export default MagicBox


